import { Route, Redirect } from 'react-router-dom';

import usePresenter from './hooks/usePresenter';

function PrivateRoute({ children, ...rest }) {
  const { user } = usePresenter();

  return (
    <Route
      {...rest}
      render={({ location }) =>
        user.username ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: '/mooglee/login',
              state: { from: location },
            }}
          />
        )
      }
    />
  );
}

export default PrivateRoute;
